<div class="login"><div class="model"></div><div class="header-title"><img class="logo" src="static/logo/logo.png"><div class="name">{{ softConfig.softName }}</div></div><div class="formModel"><div class="title">登陆</div><div class="items"><a-input placeholder="请输入账号" size="large" v-model:value="form.code"></a-input></div><div class="items"><a-input placeholder="请输入密码" size="large" type="password" @pressEnter="login" v-model:value="form.password"></a-input></div><div class="pass-text"><a-checkbox v-model="remember">记住密码</a-checkbox><a-button class="fp-button" type="link">忘记密码</a-button></div><a-button class="sure-button" block type="primary" @click="login" :loading="loading">登陆</a-button><div class="login-footer"><div class="copyright">{{softConfig.copyRightValue}}</div><div class="line"></div><div class="text">V3.0</div></div></div></div>